import { useState } from 'react';
import { Button, Form, Navbar, Table, Modal, FormControl, FormGroup } from 'react-bootstrap';
import { useDispatch, useSelector } from 'react-redux';
import { supplySlice } from './supplySlice';
import SupplyMarket from './SupplyMarket'

const SupplyButton = (props) => {
  const [show, setShow] = useState(false);
  const [amount, setamount] = useState(0)
  const [confirming, setconfirming] = useState(false)
  const dispatch = useDispatch()

  const onAmountChange = e => setamount(e.target.value)

  const handleClose = () => {
    setShow(false);
    setamount(0);
  }
  const handleShow = () => setShow(true);
  const handleConfirm = () => {
    setconfirming(true);
    setTimeout(() => {
      setShow(false)
      setconfirming(false)
      setamount(0)
    }, 3000);
  }

  const balance = props.walletAsset.balance
  const isConfirmDisabled = !!!amount || amount === 0 || amount > balance

  let body;
  let footer;
  if(confirming) {
    body = (
      <div>Confirming...</div>
    )

    footer = <div></div>
  } else {
    body = (
      <>
        <Form>
          <FormGroup>
            <Form.Label>Amount</Form.Label>
            <FormControl type="text" placeholder="Amount" value={amount} onChange={onAmountChange} />
          </FormGroup>
          <FormGroup>
            <Button>MAX</Button>
          </FormGroup>
        </Form>
        <Table>
          <tbody>
            <tr>
              <td>Balance</td>
              <td>{balance}</td>
            </tr>
          </tbody>
        </Table>
      </>
    )

    footer = (
      <>
        <Button variant="secondary" onClick={handleClose}>
          Cancel
          </Button>
        <Button variant="primary" disabled={isConfirmDisabled} onClick={handleConfirm}>
          Confirm
          </Button>
      </>
    )
  }

  return (
    <>
      <Button variant="primary" onClick={handleShow}>
        Supply
      </Button>

      <Modal show={show} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title>Supply {props.market.asset}</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          {body}
        </Modal.Body>
        <Modal.Footer>
          {footer}
        </Modal.Footer>
      </Modal>
    </>
  );
}

const Supply = () => {
  const supply = useSelector(state => state.supply)
  const wallet = useSelector(state => state.wallet)
  
  const suppliesInUsd = wallet.assets.map(asset => asset.currentSupply * supply.markets.find(s => s.asset === asset.name).usdExchange)
  const totalSupply = suppliesInUsd.reduce((acc, usd) => acc + usd, 0)

  let marketRows = supply.markets.map(market => <SupplyMarket market={market} />)
  return (
    <>
      <div 
        className="text-center"
        style={{
          height: "100px"
        }}
      >
        <h4>Supply Balance</h4>
        <h5>${totalSupply.toFixed(6)}</h5>
      </div>
      <h4>Supply Markets</h4>
      <Table>
        <thead>
          <th>Asset</th>
          <th>APY</th>
          <th>Wallet</th>
          <th>Collateral</th>
          <th></th>
        </thead>
        <tbody>
          {marketRows}
        </tbody>
      </Table>
    </>
  )
}

export default Supply