import React from 'react'
import { useSelector } from 'react-redux'
import Table from 'react-bootstrap/Table'

const BorrowMarket = () => {
  const borrow = useSelector(state => state.borrow)

  let marketRows = borrow.markets.map(mkt => {
    return (
      <tr>
        <td>{mkt.asset}</td>
        <td>{mkt.APY}%</td>
        <td>{mkt.wallet} {mkt.asset}</td>
        <td>{mkt.liquidity}</td>
      </tr>
    )
  })
  return (
    <>
      <div
        className="d-flex justify-content-center align-items-center"
        style={{
          height: "100px"
        }}
      >
        <span className="border border-primary p-2">Balance {borrow.totalBalance}</span>
      </div>
      <h4>Borrow Markets</h4>
      <Table>
        <thead>
          <th>Assets</th>
          <th>APY</th>
          <th>Wallet</th>
          <th>Liquidity</th>
        </thead>
        <tbody>
          {marketRows}
        </tbody>
      </Table>
    </>
  )
}

export default BorrowMarket