import { useState } from 'react';
import { Button, Form, Navbar, Table, Modal, FormControl, FormGroup } from 'react-bootstrap';
import { useDispatch, useSelector } from 'react-redux';
import { supplySlice } from './supplySlice';
import SupplyButton from './SupplyButton';

const SupplyMarket = props => {
  const dispatch = useDispatch()
  const wallet = useSelector(state => state.wallet)
  const getWalletAsset = (assetName) => wallet.assets.find(a => a.name == assetName)
  const walletAsset = getWalletAsset(props.market.asset)
  const balance = walletAsset ? walletAsset.balance : 0
  const onToggleChanged = (asset, value) => {
    const payload = {
      asset,
      value
    }
    dispatch(supplySlice.actions.toggleSupply(payload))
  }

  return (
    <tr>
      <td>{props.market.asset}</td>
      <td>{props.market.APY}%</td>
      <td>{balance} {props.market.asset}</td>
      <td>
        <Form>
          <Form.Check
            type="switch"
            id={"switch" + props.market.asset}
            label=""
            onChange={e => onToggleChanged(props.market.asset, e.target.value)}
            value={props.market.isOn}
          ></Form.Check>
        </Form>
      </td>
      <td>
        {balance > 0 && <SupplyButton market={props.market} walletAsset={walletAsset} />}
      </td>
    </tr>
  )
}

export default SupplyMarket