import { useState } from 'react';
import { Button, Form, Navbar, Table, Modal, FormControl, FormGroup } from 'react-bootstrap';
import { useDispatch, useSelector } from 'react-redux';
import { walletSlice } from '../wallet/walletSlice';
import { supplySlice } from './supplySlice';

const SupplyButton = (props) => {
  const [show, setShow] = useState(false);
  const [amount, setamount] = useState(0)
  const [confirming, setconfirming] = useState(false)
  const dispatch = useDispatch()

  const onAmountChange = e => setamount(e.target.value)

  const handleClose = () => {
    setShow(false);
    setamount(0);
  }
  const handleShow = () => setShow(true);
  const handleConfirm = () => {
    setconfirming(true);
    setTimeout(() => {
      dispatch(walletSlice.actions.setSupply({ amount: parseInt(amount), asset: props.walletAsset}))
      setShow(false)
      setconfirming(false)
      setamount(0)
    }, 500);
  }

  const balance = props.walletAsset.balance
  const isConfirmDisabled = !!!amount || amount === 0 || amount > balance

  let body;
  let footer;
  if (confirming) {
    body = (
      <div>Confirming...</div>
    )

    footer = <div></div>
  } else {
    body = (
      <>
        <Form>
          <FormGroup>
            <Form.Label>Amount</Form.Label>
            <FormControl type="text" placeholder="Amount" value={amount} onChange={onAmountChange} />
          </FormGroup>
        </Form>
        <Table>
          <tbody>
            <tr>
              <td>Balance</td>
              <td>{balance}</td>
            </tr>
          </tbody>
        </Table>
      </>
    )

    footer = (
      <>
        <Button variant="secondary" onClick={handleClose}>
          Cancel
          </Button>
        <Button variant="primary" disabled={isConfirmDisabled} onClick={handleConfirm}>
          Confirm
          </Button>
      </>
    )
  }

  return (
    <>
      <Button variant="primary" onClick={handleShow}>
        Supply
      </Button>

      <Modal show={show} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title>Supply {props.market.asset}</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          {body}
        </Modal.Body>
        <Modal.Footer>
          {footer}
        </Modal.Footer>
      </Modal>
    </>
  );
}

export default SupplyButton