import { createSlice } from "@reduxjs/toolkit";

export const walletSlice = createSlice({
  name: "wallet",
  initialState: {
    address: "lqTest",
    assets: [
      {
        name: "ADA",
        balance: 990,
        currentSupply: 10
      },
      {
        name: "LQ",
        balance: 500,
        currentSupply: 0
      }
    ]
  },
  reducers: {
    setSupply(state, { payload }) {
      state.assets.find(a => a.name === payload.asset.name).currentSupply += payload.amount
      state.assets.find(a => a.name === payload.asset.name).balance -= payload.amount
    }
  }
})