import { createSlice } from "@reduxjs/toolkit";

export const supplySlice = createSlice({
  name: "supply",
  initialState: {
    markets: [
      {
        asset: "ADA",
        APY: "10",
        usdExchange: 1.75127,
        isOn: false
      },
      {
        asset: "LQ",
        APY: "10",
        isOn: false,
        usdExchange: 12.23,
      }
    ],
    totalBalance: 1337
  },
  reducers: {
    toggleSupply(state, action) {
      state.markets.find(m => m.asset === action.payload.asset).isOn = true
    }
  }
})